import todolist2 from '@/components/itemList/itemList1'
import todolist1 from '@/components/itemList/itemList'
export default {
	components: {
		todolist2,
		todolist1,
	},
	onLoad() {
		console.info('index onLoad')
		let date = new Date();
		let startTime = date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate()
		this.getTodolist(startTime);
	},
	data() {
		return {
			menuArrow: false,
			index: -1,
			todolist: [],
			picker: ['考研', '四六级', '考证', '实习'],
			picker1: ['北京大学', '清华大学', '四川大学', '重庆大学'],
			title: 'Hello',
			words: '再不学习就只有继续当咸鱼咯',
			scroll: 1,
			time: '12:01',
			date: '2020-5-20',
			allDays: 31,
			today: 1,
			modalName: null,
		}
	},
	methods: {
		getTodolist(startTime) {
			console.info('调用todolist/get接口')
			console.info(startTime)
			var _self = this;
			uni.request({
				url: 'http://localhost:8070/todolist/get?salt=a&startDate=' + startTime, //请求接口
				header: {
					'content-type': 'application/x-www-form-urlencoded', //自定义请求头信息
				},
				success: (res) => {
					console.info(res)
					_self.todolist = res.data.data;
				}
				
			});
			console.info("todolist"+this.todolist)
		},
		completeTodoItem(todoid) {
			console.info(todoid + "标记为已完成")
			var _self = this;
			uni.request({
				url: 'http://localhost:8070/todolist/complete?salt=a&todoid=' + todoid,
				header: {
					'content-type': 'application/x-www-form-urlencoded', //自定义请求头信息
				},
				success: (res) => {
					if (res.data.success == 'true') {
						let startTime = '2020-6-' + this.scroll
						getTodolist(startTime)
					}
				}
			})
		},
		uncompleteTodoItem(todoid) {
			console.info(todoid + "撤销标记")
			var _self = this;
			uni.request({
				url: 'http://localhost:8070/todolist/uncomplete?salt=a&todoid=' + todoid,
				header: {
					'content-type': 'application/x-www-form-urlencoded', //自定义请求头信息
				},
				success: (res) => {
					if (res.data.success == 'true') {
						let startTime = '2020-6-' + this.scroll
						getTodolist(startTime)
					}
				}
			})
		},
		deleteTodoItem(todoid) {
			console.info(todoid + "清单删除")
			uni.request({
				url: 'http://localhost:8070/todolist/delete?salt=a&todoid=' + todoid,
				header: {
					'content-type': 'application/x-www-form-urlencoded', //自定义请求头信息
				},
				success: (res) => {
					if (res.data.success == 'true') {
						let startTime = '2020-6-' + this.scroll
						getTodolist(startTime)
					}
				}
			})
		},
		changeIndex(index) {
			this.scroll = index
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		PickerChange(e) {
			this.index = e.detail.value
		},
		TimeChange(e) {
			this.time = e.detail.value
		},
		DateChange(e) {
			this.date = e.detail.value
		},
		complete(itemid) {
			console.info(itemid)
		},
		uncomplete(itemid) {
			console.info(itemid)
		},
		deleteItem(itemid) {
			console.info(itemid)
		},
		goBack() {
			this.scroll = this.today
		}
	},
	computed: {
		days() {
			var startDate = new Date();
			this.scroll = this.today = startDate.getDate()
			var d = new Date(startDate.getFullYear(), startDate.getMonth() + 1, 0);
			this.allDays = d.getDate();
			var endDate = Date.parse("2020-12-19");
			var days = (endDate - startDate) / (1 * 24 * 60 * 60 * 1000);
			return parseInt(days);
		},
	},
	watch: {
		scroll(newV) {
			// 调用todolist/get接口
			//TODO startTime重新获取
			let startTime = '2020-6-' + newV
			this.getTodolist(startTime);
		}
	},
}
